/* src/style.css */

body {
  font-family: 'Roboto', sans-serif;
  background-color: #1a1a2e;
  color: #f5f5f5;
  text-align: center;
  margin: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
}

h1 {
  font-size: 3em;
  color: #00d4ff;
  font-family: 'Orbitron', sans-serif;
  margin-bottom: 1em;
}

/* Desktop styles */
.image-container {
  position: relative;
  width: 700px;         /* Square dimension for desktop */
  height: 700px;
  background-size: cover;
  background-position: center;
  border-radius: 15px;
  overflow: hidden;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.2);
  margin-bottom: 20px;
}

.sentence-box {
  position: absolute;
  bottom: 5%;
  left: 50%;
  transform: translateX(-50%);
  background-color: rgba(26, 26, 46, 0.8);
  color: #00d4ff;
  padding: 1.5em;
  border-radius: 10px;
  font-size: 1.3em;
  max-width: 90%;
}

.arrow-button {
  background-color: #00d4ff;
  border: none;
  padding: 0.8em 1.5em;
  font-size: 1.1em;
  cursor: pointer;
  border-radius: 30px;
  position: absolute;
  bottom: 5%;
  right: 5%;
}

.choices {
  position: absolute; /* Allow positioning relative to the container */
  bottom: 5%; /* Move 5% up from the bottom of the container */
  left: 50%; /* Center horizontally */
  transform: translateX(-50%); /* Adjust for horizontal centering */
  display: flex;
  gap: 1.5em;
  justify-content: center;
}

.choice-btn {
  background-color: #00d4ff;
  border: none;
  padding: 1em 2.5em;
  font-size: 1.2em;
  cursor: pointer;
  border-radius: 30px;
  transition: all 0.3s ease;
  bottom: 5%;
}

.choice-btn:hover {
  background-color: #008cbf;
  transform: translateY(-5px);
  box-shadow: 0 15px 20px rgba(0, 0, 0, 0.2);
}

.hover-translate {
  position: relative;
  cursor: pointer;
  text-decoration: dotted underline;
}

.hover-translate:hover::after {
  content: attr(data-translation);
  position: absolute;
  bottom: 100%;
  left: 50%;
  transform: translateX(-50%);
  background-color: #00d4ff;
  color: #1a1a2e;
  padding: 0.5em;
  border-radius: 5px;
  font-size: 0.9em;
  white-space: nowrap;
}

/* Mobile styles for screens smaller than 768px */
@media (max-width: 768px) {
  .image-container {
    width: 400px;      /* Square dimension for mobile */
    height: 400px;
  }

  .sentence-box {
    font-size: 1em;    /* Smaller text for mobile */
    padding: 1em;
  }

  .choice-btn {
    font-size: 1em;
    padding: 0.8em 2em;
  }

  .practice-link {
    display: block;
    margin-top: 20px;
    font-size: 1em;
    color: #00d4ff;
    text-decoration: underline;
    opacity: 0.8;
    cursor: pointer;
    transition: opacity 0.3s;
  }
  
  .practice-link:hover {
    opacity: 1;
  }

  .quiz-container {
    text-align: center;
    background-color: #1a1a2e;
    padding: 20px;
    border-radius: 10px;
    color: #f5f5f5;
  }
  
  .quiz-container h2 {
    color: #00d4ff;
  }
  
  .quiz-container input {
    padding: 8px;
    margin: 10px;
    font-size: 1em;
    border: 2px solid #00d4ff;
    border-radius: 5px;
  }
  
  .quiz-container button {
    background-color: #00d4ff;
    color: #1a1a2e;
    border: none;
    padding: 10px 20px;
    font-size: 1em;
    cursor: pointer;
    border-radius: 5px;
  }
  
  .quiz-container button:hover {
    background-color: #008cbf;
  }  
}
